export default ()=> {
      return (
        <div className="question" data-index="98" dangerouslySetInnerHTML = {{ __html: `
<h6>100. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token comment">// 🎉✨ This is my 100th question! ✨🎉</span>

<span class="token keyword keyword-const">const</span> output <span class="token operator">=</span> <span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token interpolation"><span class="token interpolation-punctuation punctuation">\${</span><span class="token punctuation">[</span><span class="token punctuation">]</span> <span class="token operator">&amp;&amp;</span> <span class="token string">'Im'</span><span class="token interpolation-punctuation punctuation">}</span></span><span class="token string">possible!
You should</span><span class="token interpolation"><span class="token interpolation-punctuation punctuation">\${</span><span class="token string">''</span> <span class="token operator">&amp;&amp;</span> <span class="token template-string"><span class="token template-punctuation string">\`</span><span class="token string">n't</span><span class="token template-punctuation string">\`</span></span><span class="token interpolation-punctuation punctuation">}</span></span><span class="token string"> see a therapist after so much JavaScript lol</span><span class="token template-punctuation string">\`</span></span>
</code></pre>
<ul>
<li>A: <code>possible! You should see a therapist after so much JavaScript lol</code></li>
<li>B: <code>Impossible! You should see a therapist after so much JavaScript lol</code></li>
<li>C: <code>possible! You shouldn't see a therapist after so much JavaScript lol</code></li>
<li>D: <code>Impossible! You shouldn't see a therapist after so much JavaScript lol</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p><code>[]</code>是一个真值。 使用<code>&amp;&amp;</code>运算符，如果左侧值是真值，则返回右侧值。 在这种情况下，左侧值<code>[]</code>是一个真值，所以返回<code>Im</code>。</p>
<p><code>&quot;&quot;</code>是一个假值。 如果左侧值是假的，则不返回任何内容。 <code>n't</code>不会被退回。</p>
</p>
</details>
` }}></div>
      );
    }